
*{
	margin: 0;
	padding: 0;
}
.clearfix:after {
    display: block;
    visibility: hidden;
    font-size: 0;
    clear: both;
    width: 0;
    height: 0;
    content: '.';
}
.container {
	width: 1200px;
	margin: 0 auto;
}
ul,li{
    list-style: none;
}
.first-floor{
    background: #fff;
}
